.userBalance {
  display: flex;
  align-items: center;
  padding-left: 20px;
  height: 50px;
  cursor:pointer;
  transition-duration: .3s;
  border-left:3px solid transparent;
  span {
    color: rgba(255, 255, 255, 0.87);
    font-size: 20px;
    margin-left: 10px;
    position: relative;
    line-height: 20px;
    font-weight: 600;
    &:before {
      content: '';
      position: absolute;
      width: 100%;
      bottom: -3px;
      height: 2px;
      background: rgba(255, 255, 255, 0.25);
    }
  }
  span.afterpoint {
    color: rgba(255, 255, 255, 0.38);
    font-size: 20px;
    margin-left: 0px;
  }
  .add-button {
    background: url('./add.svg') center center no-repeat;
    width: 28px;
    height: 28px;
    margin-left: 6px;
  }
  &--active, &:hover {
    transition-duration: .3s;
    border-left:3px solid #6279C3;
    background: rgba(67,71,83, 0.4);
  }
}
.userName {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 15px;
  color: white;
  text-decoration: none;
  height: 64px;
  width: 100%;
  border-top: 1px solid #3e414c;
  cursor:pointer;
  img {
    max-width: 36px;
    border-radius: 50%;
    margin-right: 10px;
  }

  span {
    color: rgba(255, 255, 255, 0.6);
    letter-spacing: -0.5px;
    margin-left: 3px;
    margin-top: -5px;
  }

  &--active, &:hover {
    background: rgba(67, 71, 83, 0.4);
    transition-duration: .3s;
  }
  &__data {
    display: flex;
    align-items: center;
  }
}


.appLanguage {
  display: flex;
  height: 100%;
  align-items: center;
  float: right;
  padding: 0 20px;
  &--active, &:hover {
    background: rgba(67, 71, 83, 0.4);
    transition-duration: .3s;
  }
}
.appLanguageDropdown {
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.4);
  width: 100%;
  height: 100vh;
  z-index: 10;
  .content{
    position: fixed;
    left: 0;
    bottom: 0;
    background: #2C2F38;
    width: 280px;
    box-shadow: 0px 8px 10px rgba(12, 13, 15, 0.2), 0px 6px 30px rgba(12, 13, 15, 0.12), 0px 16px 24px rgba(12, 13, 15, 0.14);
    border-top-right-radius: 8px;
    padding: 10px 0;
    div {
      padding: 10px 20px;
      background: rgba(255,255,255,0);
      transition-duration: .3s;
      display: flex;
      flex-direction:row;
      align-items:middle;
      border-left:3px solid transparent;
      cursor:pointer;
      img {
        margin-right: 15px;
      }
      &:hover {
        background: rgba(255,255,255,0.08);
        transition-duration: .3s;
        border-left:3px solid #6279C3;
      }
      &.currentLang {
        background: rgba(255,255,255,0.08);
        border-left:3px solid #6279C3;
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .usercenter {
    .userBalance {
      display: none;
    }
  }
  .userName {
    flex-direction: column;
    height: auto;
    padding-left: 0;
    border-top: 0;
    &> div {
      padding: 25px 0;
    }
    &__data {
      padding: 10px 0 !important;
      flex-direction:column;
      width: 100%;

      border-bottom: 1px solid #3e414c;
      img {
        margin: 0 0 10px 0;
      }
    }

  }
}